body{
  background-color: rgb(30, 35, 61);
  color: white;
}


.app-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movie-container {
  width: 250px;
  margin: 16px;
  background-color: rgb(81, 96, 161);
  color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  border: 1px solid rgb(104, 123, 139);

}

.movie-container img{
  max-width: 100%;
}

.movie-info{
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
}

.movie-info h4 {
  margin: 0px;
}

.movie-info span {
  margin-left: 13px;
}
